@import url(https://fonts.googleapis.com/css2?family=Inconsolata:wght@300;400&family=Rubik:ital,wght@0,400;1,300;1,400&display=swap);
* {
  box-sizing:border-box;
  padding: 0;
  margin:0
}

a{
  text-decoration: none;
  color: lightgray;
}


body {
  margin: 0;
  font-family: 'Rubik', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:#282c34;
  color:white;
  max-width:1100px;

  margin:0 auto;
}



.App{padding:20px;text-align:left}.won-card{background-color:#54e1a3}.lost-card{background-color:#e15454}

.MatchDetailCard{display:grid;grid-template-columns:1fr 1fr;padding:10px;margin-bottom:20px}.MatchDetailCard .match-date,.MatchDetailCard .match-venue,.MatchDetailCard .match-result{margin-top:10px}.MatchDetailCard .additional-detail{text-align:right}.MatchDetailCard .additional-detail>p{margin-bottom:20px}

.MatchSmallCard{padding:10px;margin-bottom:20px}.MatchSmallCard .match-result{margin-top:10px}

.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 0.4fr;grid-column-gap:10px;grid-row-gap:0}.TeamPage .team-name-section{grid-column:span 3;justify-self:start;align-self:center}.TeamPage .team-name-section span{color:red}.TeamPage .team-name{font-size:40px;margin-bottom:0px}.TeamPage .win-loss-section{justify-self:center;padding:30px;text-align:center}.TeamPage .match-detail-section{grid-column:span 4}.TeamPage .more-link{align-self:center;justify-self:center;font-size:1.5rem}

.MatchPage{display:grid;grid-template-columns:1fr 5fr;grid-column-gap:20px}.MatchPage .year-selector{justify-self:end;padding:20px}.MatchPage .page-heading{margin:20px auto}.MatchPage .no-match-text{color:red}

.YearSelector{text-align:right;list-style-type:none}.YearSelector li{margin:10px 0}

.HomePage .header-section{margin:30px 0;text-align:center}.HomePage .team-grid{display:grid;grid-template-columns:1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}

.TeamTile{text-align:center;border:1px solid white;height:170px;align-self:end;padding:20px}

