.TeamPage{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 0.4fr;
  grid-column-gap: 10px;
  grid-row-gap: 0;



  .team-name-section{
    grid-column: span 3;
    justify-self: start; //horizontal
    align-self: center; //verical

    span{
      color: red;

    }
  }

  .team-name{
    font-size: 40px;
    margin-bottom: 0px;
  }

  .win-loss-section{
    justify-self: center;
    padding: 30px;
    text-align: center;
  }

  .match-detail-section{
    grid-column: span 4;
  }
  .more-link{
    align-self: center;
    justify-self: center;
    font-size: 1.5rem;
  }
}